import React from "react";

export default function TaskInput(props) {
  return (
    <>
      {props.activeProject === "" ||
      props.activeProject === "All" ||
      props.activeProject === "Important" ||
      props.activeProject === "Today" ||
      props.activeProject === "This Week" ||
      props.activeProject === "Completed" ? (
        <div className="task-input">
          <input
            className="input-task"
            placeholder="Create or open a project to add a task"
            disabled
          />
          <button className="add-task" disabled>
            <i className="fa-solid fa-plus"></i>
          </button>
        </div>
      ) : (
        <div className="task-input">
          <input
            className="input-task"
            onKeyPress={props.onKeyPress}
            placeholder={"Enter task here"}
          />
          <button className="add-task" onClick={props.onClick}>
            <i className="fa-solid fa-plus"></i>
          </button>
        </div>
      )}
    </>
  );
}
