import React from "react";

export default function ProjectTitle(props) {
  return (
    <div className="project-title-box">
      <h1 className="project-title" onClick={() => props.editProject()}>
        {props.activeProject}
      </h1>
      <input
        className="edit-project"
        onKeyPress={props.onKeyPress}
        onBlur={props.onBlur}
      />
      <button className="help" onClick={() => props.startTutorial()}>
        <i className="fa-solid fa-circle-question"></i>
      </button>
      <div className="tutorial-preview tutorial-box box arrow-top-right">
        First time? Click this icon to start a tutorial that teaches you how to
        use this app and its features!
      </div>
    </div>
  );
}
