import React, { useState } from "react";
import { nanoid } from "nanoid";
import { slide as Navigation } from "react-burger-menu";
import "core-js/actual"; // <- at the top of your entry point

import "./App.css";
import "./components/project/tutorial.css";

// Navigation
import ProjectNav from "./components/navigation/ProjectNav";
import ProjectNavItem from "./components/navigation/ProjectNavItem";
import TopNav from "./components/navigation/TopNav";
import TopNavItem from "./components/navigation/TopNavItem";

// Project and tasklist
import Project from "./components/project/Project";
import ProjectTitle from "./components/project/ProjectTitle";
import TaskInput from "./components/project/TaskInput";
import Tasklist from "./components/project/Tasklist";
import Task from "./components/project/Task";
import Modal from "./components/project/Modal";
import Completed from "./components/project/Completed";
import Tutorial from "./components/project/Tutorial";
import { NO_DATA_ALLOWED_ERR } from "domexception";

const todaysDate = (separator = "-") => {
  let newDate = new Date();
  let date = newDate.getDate();
  let month = newDate.getMonth() + 1;
  let year = newDate.getFullYear();

  console.log(
    String(
      `${year}${separator}${month < 10 ? `0${month}` : `${month}`}${separator}${
        date < 10 ? `0${date}` : `${date}`
      }`
    )
  );

  return String(
    `${year}${separator}${month < 10 ? `0${month}` : `${month}`}${separator}${
      date < 10 ? `0${date}` : `${date}`
    }`
  );
};

const formattedDate = (num) => {
  const date = new Date();
  date.setDate(date.getDate() + num);
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  let day = date.getDate();

  return String(
    `${year}${"-"}${month < 10 ? `0${month}` : `${month}`}${"-"}${
      day < 10 ? `0${day}` : `${day}`
    }`
  );
};

const topNavItems = ["All", "Important", "Today", "This Week", "Completed"];

function App(props) {
  const [tasks, setTasks] = useState(props.tasks);
  const [projects, setProjects] = useState(props.projects);
  const [activeProject, setActiveProject] = useState("All");
  const [openMenu, setOpenMenu] = useState(false);
  const [tutorialStart, setTutorialStart] = useState(false);
  const [step, setStep] = useState(0);

  const topNavList = topNavItems.map((item, index) => {
    return (
      <TopNavItem title={item} key={index} onClick={(e) => selectProject(e)} />
    );
  });

  const addProject = () => {
    const projectInput = document.querySelector(".project-input");
    projectInput.style.display = "block";
    projectInput.focus();

    if (tutorialStart) {
      if (step === 1) {
        const box1 = document.querySelector(".box-1");
        const box2 = document.querySelector(".box-2");
        box1.style.display = "none";
        box2.style.display = "flex";
        setStep(2);
      }
    }
  };

  const submitProject = (e) => {
    let max_chars = 25;
    if (e.key === "Enter") {
      console.log("hi");
      if (e.target.value === "" || e.target.value === " ") {
        alert("ERROR: Project name cannot be empty.");
      } else if (e.target.value.toString().length > max_chars) {
        e.target.value = e.target.value.substr(0, max_chars);
        alert(
          "ERROR: Please limit your project titles to 25 characters or less."
        );
      } else if (projects.includes(e.target.value)) {
        alert(
          "ERROR: Duplicate project titles may cause errors. Please change your title."
        );
        e.target.value = "";
      } else {
        if (tutorialStart) {
          if (step === 2) {
            const box2 = document.querySelector(".box-2");
            const box3 = document.querySelector(".box-3");
            box2.style.display = "none";
            box3.style.display = "flex";
            setStep(3);
          }
        }
        const newProject = e.target.value;
        setProjects([...projects, newProject]);
        localStorage.setItem("projects", JSON.stringify(projects));
        e.target.style.display = "none";
        e.target.value = "";
        setActiveProject(newProject);
        handleCloseMenu();
      }
    }
  };

  const deleteProject = (e) => {
    if (
      window.confirm(
        "Are you sure you want to delete this project and all of its tasks?"
      )
    ) {
      if (tutorialStart) {
        if (step === 14) {
          const box14 = document.querySelector(".box-14");
          const box15 = document.querySelector(".box-15");
          box14.style.display = "none";
          box15.style.display = "flex";
          setStep(15);
        }
      }
      const updatedTasks = tasks.filter((task) => {
        return task.project !== e.target.parentElement.children[0].textContent;
      });
      setTasks(updatedTasks);
      const updatedProjects = projects.filter((project) => {
        return project !== e.target.parentElement.children[0].textContent;
      });
      setProjects(updatedProjects);
    }
    setActiveProject("All");
  };

  const projectNavList = projects.map((item, index) => {
    return (
      <ProjectNavItem
        title={item}
        key={index}
        onClick={(e) => selectProject(e)}
        deleteProject={deleteProject}
      />
    );
  });

  const selectProject = (e) => {
    handleCloseMenu();
    setActiveProject(e.target.textContent);

    console.log(tasks);
  };

  const editProject = () => {
    if (
      activeProject === "All" ||
      activeProject === "Important" ||
      activeProject === "Today" ||
      activeProject === "This Week" ||
      activeProject === "Completed"
    ) {
      return;
    } else {
      const projectInput = document.querySelector(".edit-project");
      const projectTitle = document.querySelector(".project-title");

      projectTitle.style.display = "none";
      projectInput.style.display = "block";

      projectInput.value = projectTitle.textContent;

      projectInput.focus();
    }
    console.log(activeProject);
  };

  const submitProjectBlur = (e) => {
    let max_chars = 25;
    const projectTitle = document.querySelector(".project-title");
    const projectNavItems = document.querySelectorAll(".project-nav-title");
    let originalName = projectTitle.textContent;
    console.log(`Original name: ${originalName}`);

    if (e.target.value === "" || e.target.value === " ") {
      e.target.value = originalName;
      projectTitle.style.display = "block";
      e.target.style.display = "none";
    } else if (projects.includes(e.target.value)) {
      if (e.target.value !== originalName) {
        alert(
          "ERROR: Duplicate project titles may cause errors. Please change your title."
        );

        e.target.value = "";
      } else {
        setActiveProject(e.target.value);

        for (let i = 0; i < projects.length; i++) {
          if (projects[i] === projectTitle.textContent) {
            projects[i] = e.target.value;
          }
        }

        for (let i = 0; i < projectNavItems.length; i++) {
          if (projectNavItems[i].textContent === projectTitle.textContent) {
            projectNavItems[i].textContent = e.target.value;
          }
        }

        for (let i = 0; i < tasks.length; i++) {
          if (tasks[i].project === projectTitle.textContent) {
            tasks[i].project = e.target.value;
          }
        }

        projectTitle.textContent = e.target.value;
        e.target.style.display = "none";
        projectTitle.style.display = "block";
        setProjects(projects);
      }
    } else if (e.target.value.toString().length > max_chars) {
      e.target.value = e.target.value.substr(0, max_chars);
      alert(
        "ERROR: Please limit your project titles to 25 characters or less."
      );
    } else {
      setActiveProject(e.target.value);

      for (let i = 0; i < projects.length; i++) {
        if (projects[i] === projectTitle.textContent) {
          projects[i] = e.target.value;
        }
      }

      for (let i = 0; i < projectNavItems.length; i++) {
        if (projectNavItems[i].textContent === projectTitle.textContent) {
          projectNavItems[i].textContent = e.target.value;
        }
      }

      for (let i = 0; i < tasks.length; i++) {
        if (tasks[i].project === projectTitle.textContent) {
          tasks[i].project = e.target.value;
        }
      }

      projectTitle.textContent = e.target.value;
      e.target.style.display = "none";
      projectTitle.style.display = "block";
      setProjects(projects);
    }
  };

  const submitProjectEdit = (e) => {
    const projectTitle = document.querySelector(".project-title");

    const projectNavItems = document.querySelectorAll(".project-nav-title");

    let originalName = projectTitle.textContent;
    console.log(`Original name: ${originalName}`);
    let max_chars = 25;
    if (e.key === "Enter") {
      if (e.target.value === "" || e.target.value === " ") {
        alert("ERROR: Project name cannot be empty.");
      } else if (e.target.value.toString().length > max_chars) {
        e.target.value = e.target.value.substr(0, max_chars);
        alert(
          "ERROR: Please limit your project titles to 25 characters or less."
        );
      } else if (projects.includes(e.target.value)) {
        if (e.target.value !== originalName) {
          alert(
            "ERROR: Duplicate project titles may cause errors. Please change your title."
          );

          e.target.value = "";
        } else {
          setActiveProject(e.target.value);

          for (let i = 0; i < projects.length; i++) {
            if (projects[i] === projectTitle.textContent) {
              projects[i] = e.target.value;
            }
          }

          for (let i = 0; i < projectNavItems.length; i++) {
            if (projectNavItems[i].textContent === projectTitle.textContent) {
              projectNavItems[i].textContent = e.target.value;
            }
          }

          for (let i = 0; i < tasks.length; i++) {
            if (tasks[i].project === projectTitle.textContent) {
              tasks[i].project = e.target.value;
            }
          }

          projectTitle.textContent = e.target.value;
          e.target.style.display = "none";
          projectTitle.style.display = "block";
          setProjects(projects);
        }
      } else {
        if (tutorialStart) {
          if (step === 3) {
            const box3 = document.querySelector(".box-3");
            const box4 = document.querySelector(".box-4");
            box3.style.display = "none";
            box4.style.display = "flex";
            setStep(4);
          }
        }
        setActiveProject(e.target.value);

        for (let i = 0; i < projects.length; i++) {
          if (projects[i] === projectTitle.textContent) {
            projects[i] = e.target.value;
          }
        }

        for (let i = 0; i < projectNavItems.length; i++) {
          if (projectNavItems[i].textContent === projectTitle.textContent) {
            projectNavItems[i].textContent = e.target.value;
          }
        }

        for (let i = 0; i < tasks.length; i++) {
          if (tasks[i].project === projectTitle.textContent) {
            tasks[i].project = e.target.value;
          }
        }

        projectTitle.textContent = e.target.value;
        e.target.style.display = "none";
        projectTitle.style.display = "block";
        setProjects(projects);
      }
    }
  };

  const enterTask = (e) => {
    if (e.key === "Enter") {
      addTask();
    }
  };

  const addTask = () => {
    if (tutorialStart) {
      if (step === 4) {
        const box4 = document.querySelector(".box-4");
        const box5 = document.querySelector(".box-5");
        box4.style.display = "none";
        box5.style.display = "flex";
        setStep(5);
      }
    }
    const taskInput = document.querySelector(".input-task");
    if (taskInput.value === "" || taskInput.value === " ") {
      alert("ERROR: Task cannot be blank.");
    } else {
      const newTask = {
        title: taskInput.value,
        id: nanoid(),
        note: "",
        dueDate: "",
        important: false,
        completed: false,
        project: activeProject,
      };
      taskInput.value = "";
      taskInput.focus();
      setTasks([...tasks, newTask]);
      localStorage.setItem("tasks", JSON.stringify(tasks));
    }
  };

  const editTask = (e) => {
    e.target.style.display = "none";
    e.target.parentElement.children[1].style.display = "block";
    e.target.parentElement.children[1].value = e.target.textContent;
    e.target.parentElement.children[1].focus();
  };

  const submitEditTask = (e, id) => {
    if (e.key === "Enter") {
      if (e.target.value === "" || e.target.value === " ") {
        alert("ERROR: Task cannot be blank.");
        e.target.value = "Please edit me.";
        tasks.forEach((task) => {
          if (task.id === id) {
            task.title = e.target.value;
          }
        });
        e.target.style.display = "none";
        e.target.parentElement.children[0].style.display = "block";
        e.target.parentElement.children[0].textContent = e.target.value;
      } else {
        if (tutorialStart) {
          if (step === 5) {
            const box5 = document.querySelector(".box-5");
            const box6 = document.querySelector(".box-6");
            box5.style.display = "none";
            box6.style.display = "flex";
            setStep(6);
          }
        }

        const updatedTasks = tasks.map((task) => {
          if (task.id === id) {
            return {
              ...task,
              title: e.target.value,
            };
          }
          return task;
        });
        setTasks(updatedTasks);
        e.target.style.display = "none";
        e.target.parentElement.children[0].style.display = "block";
        e.target.parentElement.children[0].textContent = e.target.value;
      }
    }
  };

  const blurTask = (e, id) => {
    if (e.target.value === "" || e.target.value === " ") {
      alert("ERROR: Task cannot be blank.");
      e.target.value = "Please edit me.";
      tasks.forEach((task) => {
        if (task.id === id) {
          task.title = e.target.value;
        }
      });
      e.target.style.display = "none";
      e.target.parentElement.children[0].style.display = "block";
      e.target.parentElement.children[0].textContent = e.target.value;
    } else {
      const updatedTasks = tasks.map((task) => {
        if (task.id === id) {
          return {
            ...task,
            title: e.target.value,
          };
        }
        return task;
      });
      setTasks(updatedTasks);
      e.target.style.display = "none";
      e.target.parentElement.children[0].style.display = "block";
      e.target.parentElement.children[0].textContent = e.target.value;
    }
  };

  const deleteTask = (id) => {
    if (window.confirm("Are you sure you want to delete this task?")) {
      if (tutorialStart) {
        if (step === 11) {
          const box11 = document.querySelector(".box-11");
          const box12 = document.querySelector(".box-12");
          box11.style.display = "none";
          box12.style.display = "flex";
          setStep(12);
        }
      }
      const updatedTasks = tasks.filter((task) => {
        return task.id !== id;
      });
      setTasks(updatedTasks);
      localStorage.setItem("tasks", JSON.stringify(updatedTasks));
    }
  };

  const toggleImportance = (id) => {
    if (tutorialStart) {
      if (step === 8) {
        const box8 = document.querySelector(".box-8");
        const box9 = document.querySelector(".box-9");
        box8.style.display = "none";
        box9.style.display = "flex";
        setStep(9);
      }
    }
    const updatedTasks = tasks.map((task) => {
      if (task.id === id) {
        return { ...task, important: !task.important };
      }
      return task;
    });
    setTasks(updatedTasks);
    localStorage.setItem("tasks", JSON.stringify(updatedTasks));
  };

  const toggleCompleted = (id) => {
    if (tutorialStart) {
      if (step === 9) {
        const box9 = document.querySelector(".box-9");
        const box10 = document.querySelector(".box-10");
        box9.style.display = "none";
        box10.style.display = "flex";
        setStep(10);
      }
    }
    const updatedTasks = tasks.map((task) => {
      if (id === task.id) {
        return { ...task, completed: !task.completed };
      }
      return task;
    });
    setTasks(updatedTasks);
  };

  const toggleTaskMenu = (e) => {
    if (e.target.parentElement.children[2].style.display === "block") {
      e.target.parentElement.children[2].style.display = "none";
    } else {
      e.target.parentElement.children[2].style.display = "block";
    }
  };

  const openModal = (e) => {
    if (tutorialStart) {
      if (step === 6) {
        const box6 = document.querySelector(".box-6");
        const box7 = document.querySelector(".box-7");
        box6.style.display = "none";
        box7.style.display = "flex";
        setStep(7);
      }
    }

    const modalFull = document.querySelector(".modal-full");
    const modalTitle = document.querySelector(".modal-title-input");
    const modalDescription = document.querySelector(".modal-description-input");
    const modalDate = document.querySelector(".modal-duedate-input");

    modalFull.setAttribute(
      "class",
      `${e.target.parentElement.parentElement.id} modal-full`
    );

    let modalTask = modalFull.getAttribute("class");

    tasks.forEach((task) => {
      if (task.id + " modal-full" === modalTask) {
        modalTitle.value = task.title;
        modalDescription.value = task.note;
        modalDate.value = task.dueDate;
      }
    });

    modalFull.style.display = "block";
  };

  const closeModal = () => {
    const modal = document.querySelector(".modal-full");
    modal.style.display = "none";
  };

  const submitModal = () => {
    if (tutorialStart) {
      if (step === 7) {
        const box7 = document.querySelector(".box-7");
        const box8 = document.querySelector(".box-8");
        box7.style.display = "none";
        box8.style.display = "flex";
        setStep(8);
      }
    }
    const modalFull = document.querySelector(".modal-full");
    const modalTitle = document.querySelector(".modal-title-input");
    const modalDescription = document.querySelector(".modal-description-input");
    const modalDate = document.querySelector(".modal-duedate-input");

    let modalTask = modalFull.getAttribute("class");

    if (modalTitle.value === "" || modalTitle.value === " ") {
      alert("ERROR: Task title cannot be blank.");
    } else {
      const updatedTasks = tasks.map((task) => {
        if (modalTask === task.id + " modal-full") {
          return {
            ...task,
            title: modalTitle.value,
            note: modalDescription.value,
            dueDate: modalDate.value,
          };
        }
        return task;
      });
      setTasks(updatedTasks);
      localStorage.setItem("tasks", JSON.stringify(updatedTasks));
      console.log(updatedTasks);
      console.log(todaysDate("-"));
      console.log(formattedDate(7));
      modalFull.style.display = "none";
    }
  };

  const taskList = tasks.map((task) => {
    if (
      (!task.completed &&
        ((task.important === true && activeProject === "Important") ||
          (task.dueDate === todaysDate("-") && activeProject === "Today") ||
          ((task.dueDate === formattedDate(1) ||
            task.dueDate === formattedDate(2) ||
            task.dueDate === formattedDate(3) ||
            task.dueDate === formattedDate(4) ||
            task.dueDate === formattedDate(5) ||
            task.dueDate === formattedDate(6) ||
            task.dueDate === formattedDate(7)) &&
            activeProject === "This Week") ||
          activeProject === "All")) ||
      (task.completed && activeProject === "Completed") ||
      (activeProject === task.project && !task.completed)
    ) {
      return (
        <Task
          title={task.title}
          dueDate={task.dueDate}
          note={task.note}
          id={task.id}
          important={task.important}
          completed={task.completed}
          editTask={editTask}
          deleteTask={deleteTask}
          submitEditTask={submitEditTask}
          blurTask={blurTask}
          openModal={openModal}
          toggleImportance={toggleImportance}
          toggleCompleted={toggleCompleted}
          toggleTaskMenu={toggleTaskMenu}
        />
      );
    }
  });

  const completedList = tasks.map((task) => {
    if (task.project === activeProject && task.completed === true) {
      return (
        <Task
          title={task.title}
          dueDate={task.dueDate}
          note={task.note}
          id={task.id}
          important={task.important}
          completed={task.completed}
          editTask={editTask}
          deleteTask={deleteTask}
          submitEditTask={submitEditTask}
          blurTask={blurTask}
          openModal={openModal}
          toggleImportance={toggleImportance}
          toggleCompleted={toggleCompleted}
        />
      );
    }
  });

  const handleCompleted = () => {
    if (tutorialStart) {
      if (step === 10) {
        const box10 = document.querySelector(".box-10");
        const box11 = document.querySelector(".box-11");
        box10.style.display = "none";
        box11.style.display = "flex";
        setStep(11);
      }
    }
    const rightArrow = document.querySelector(".completed-closed");
    const downArrow = document.querySelector(".completed-open");
    const completedList = document.querySelector(".completed-list");

    if (rightArrow.style.display === "inline-block") {
      rightArrow.style.display = "none";
      downArrow.style.display = "inline-block";
      completedList.style.display = "block";
    } else {
      rightArrow.style.display = "inline-block";
      downArrow.style.display = "none";
      completedList.style.display = "none";
    }
  };

  const handleStateChange = (state) => {
    setOpenMenu(state.openMenu);
  };

  const handleIsOpen = () => {
    setOpenMenu(!openMenu);
  };

  const handleCloseMenu = () => {
    setOpenMenu(false);
  };

  const startTutorial = () => {
    const menuIcon = document.querySelector("#react-burger-menu-btn");
    const tutorial1 = document.querySelector(".box-0");
    tutorial1.style.display = "flex";
    setTutorialStart(true);
    setStep(0);
  };

  const nextTutorial = () => {
    if (tutorialStart) {
      if (step === 13) {
        const box13 = document.querySelector(".box-13");
        const box14 = document.querySelector(".box-14");
        box13.style.display = "none";
        box14.style.display = "flex";
        setStep(14);
      }
    }
  };

  const endTutorial = () => {
    const currentBox = document.querySelector(`.box-${step}`);
    currentBox.style.display = "none";
    setStep(0);
    setTutorialStart(false);
  };

  function myDisplayer(some) {
    console.log(some);
  }

  let myPromise = new Promise(function (resolve, reject) {
    if (openMenu && tutorialStart) {
      resolve("Yes");
    } else {
      reject("No");
    }
  });

  myPromise.then(
    function (value) {
      if (step === 0) {
        const box0 = document.querySelector(".box-0");
        const box1 = document.querySelector(".box-1");
        box0.style.display = "none";
        box1.style.display = "flex";
        setStep(1);
      } else if (step === 12) {
        const box12 = document.querySelector(".box-12");
        const box13 = document.querySelector(".box-13");
        box12.style.display = "none";
        box13.style.display = "flex";
        setStep(13);
      }
      console.log(value);
    },
    function (error) {
      myDisplayer(error);
    }
  );

  localStorage.setItem("projects", JSON.stringify(projects));
  localStorage.setItem("tasks", JSON.stringify(tasks));

  return (
    <div className="App">
      <Navigation
        itemListElement="div"
        onStateChange={handleStateChange}
        isOpen={openMenu}
        onOpen={handleIsOpen}
        onClose={handleCloseMenu}
        customBurgerIcon={<i className="fa-solid fa-bars"></i>}
        burgerButtonClassName={"open-nav"}
        customCrossIcon={<i className="fa-solid fa-xmark" />}
        crossButtonClassName={"close-nav"}
      >
        <h2 className="app-title">Tasklist App</h2>

        <div className="navlists">
          <TopNav>{topNavList}</TopNav>
          <ProjectNav onKeyPress={(e) => submitProject(e)}>
            {projectNavList}
          </ProjectNav>
        </div>
        <button className="add-project" onClick={addProject}>
          Add Project
        </button>
      </Navigation>
      <Project>
        <ProjectTitle
          editProject={editProject}
          onKeyPress={(e) => submitProjectEdit(e)}
          onBlur={(e) => submitProjectBlur(e)}
          activeProject={activeProject}
          startTutorial={startTutorial}
        />
        <TaskInput
          onClick={addTask}
          onKeyPress={(e) => enterTask(e)}
          activeProject={activeProject}
        ></TaskInput>
        <Tasklist>
          <h3>Tasks</h3>
          {taskList}
        </Tasklist>
        {activeProject !== "All" &&
        activeProject !== "Today" &&
        activeProject !== "This Week" &&
        activeProject !== "Important" &&
        activeProject !== "Completed" &&
        tasks.map(
          (task) => task.completed && task.project === activeProject
        ) ? (
          <Completed onClick={handleCompleted}>{completedList}</Completed>
        ) : (
          ""
        )}
      </Project>
      <Modal closeModal={closeModal} submitModal={submitModal} />
      <Tutorial step={step} onClick={nextTutorial} endTutorial={endTutorial} />
    </div>
  );
}

export default App;
