import React from "react";
import TutorialBox from "./TutorialBox";
import "./tutorial.css";

const tutorialBoxes = [
  {
    title:
      "You need a project in which you can add tasks. Click on this menu icon.",
    classN: "box arrow-left-top",
  },
  {
    title: "To add a project, click on the Add Project button here.",
    classN: "box arrow-bottom-left",
  },
  {
    title: "Type your project title into this box and press 'Enter'.",
    classN: "box arrow-bottom-left",
  },
  {
    title:
      "Great! You've created a project. Edit the project title by clicking on it, typing in a new title, and pressing 'Enter' when done.",
    classN: "box arrow-top-center",
  },
  {
    title:
      "Now let's add a task. Enter your task into this box and press 'Enter' or click the + button to add it to your project.",
    classN: "box arrow-bottom-left",
  },
  {
    title:
      "You can easily edit your task by clicking on it. Do so now and change your task text, and press 'Enter' when you're done.",
    classN: "box arrow-top-center",
  },
  {
    title:
      "Let's add a couple of details to this task. Click this pencil icon.",
    classN: "box arrow-top-right",
  },
  {
    title:
      "Add some notes and/or a due date to this task, and press 'Submit' when you're done.",
    classN: "box-regular",
  },
  {
    title: "Mark this task as 'Important' by clicking the star icon here.",
    classN: "box arrow-top-right",
  },
  {
    title: "Mark this task as 'Completed' by clicking this checkbox.",
    classN: "box arrow-top-left",
  },
  {
    title:
      "Completed tasks get moved to the 'Completed' section. You can hide this section by clicking the 'Completed' title. Try it out.",
    classN: "box arrow-bottom-center",
  },
  {
    title:
      "Good job! After you open the Completed list again, let's delete this task by clicking the trash can icon and clicking 'Ok' on the confirmation window that pops up.",
    classN: "box arrow-top-right",
  },
  {
    title:
      "Almost done! Click the menu icon once more to open the navigation menu.",
    classN: "box arrow-left-top",
  },
  {
    title:
      "You can click these filters to view all tasks of a particular type. Note that, except for the 'Completed' filter, these filters only show uncompleted tasks.",
    classN: "box arrow-top-center",
    btn: "Next",
  },
  {
    title:
      "Finally, let's delete this sample project. Hover over your project title here, or click on it if you're on a mobile device. Then click on the trash can icon that appears and press 'Ok' on the confirmation window.",
    classN: "box arrow-bottom-right",
  },
  {
    title:
      "That's all there is to it! We hope you found this tutorial useful. Happy productivity!",
    classN: "box-regular",
  },
];

export default function Tutorial(props) {
  return (
    <>
      {tutorialBoxes.map((box, index) => {
        return (
          <TutorialBox
            step={props.step}
            title={box.title}
            btn={"Next"}
            classN={box.classN}
            index={index}
            onClick={props.onClick}
            endTutorial={props.endTutorial}
          />
        );
      })}
    </>
  );
}
