import React from "react";

export default function ProjectNavItem(props) {
  return (
    <li className="project-nav-item" onClick={props.onClick}>
      <span className="project-nav-title">{props.title}</span>
      <button
        className="project-delete"
        onClick={(e) => props.deleteProject(e)}
      >
        <i className="fa-regular fa-trash-can"></i>
      </button>
    </li>
  );
}
