import React from "react";

var classNames = require("classnames");

export default function Task(props) {
  const importantClasses = classNames({
    importantOn: props.important,
    importantOff: !props.important,
  });

  return (
    <li className="task" id={props.id}>
      <div className="task-left">
        <button
          id={props.id}
          className="task-completed"
          onClick={() => props.toggleCompleted(props.id)}
        >
          {props.completed ? (
            <i className="fi fi-sr-checkbox" />
          ) : (
            <i className="fi fi-sr-square"></i>
          )}
        </button>
        <div className="task-details">
          <div className="top-details">
            <span className="task-title" onClick={(e) => props.editTask(e)}>
              {props.title}
            </span>
            <input
              type="text"
              className="task-title-edit"
              ref={(input) => input && input.focus()}
              onKeyPress={(e) => props.submitEditTask(e, props.id)}
              onBlur={(e) => props.blurTask(e, props.id)}
            />
          </div>
          {props.note !== "" && props.dueDate === "" ? (
            <div className="bottom-details">
              <span className="task-note">
                <i className="fa-regular fa-note-sticky bottom-icon"></i> Note
              </span>
            </div>
          ) : props.note === "" && props.dueDate !== "" ? (
            <div className="bottom-details">
              <span className="task-date">
                <i className="fa-regular fa-calendar bottom-icon"></i>{" "}
                {props.dueDate}
              </span>
            </div>
          ) : props.note !== "" && props.dueDate !== "" ? (
            <div className="bottom-details">
              <span className="task-date">
                <i className="fa-regular fa-calendar bottom-icon"></i>{" "}
                {props.dueDate}
              </span>
              <span className="divider">•</span>
              <span className="task-note">
                <i className="fa-regular fa-note-sticky bottom-icon"></i> Note
              </span>
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
      <div className="task-right">
        <button
          className={importantClasses}
          onClick={() => props.toggleImportance(props.id)}
        >
          {props.important ? (
            <i className="fi fi-sr-star" />
          ) : (
            <i className="fi fi-rr-star" />
          )}
        </button>
        <button
          className="task-edit task-btn"
          onClick={(e) => props.openModal(e)}
        >
          <i className="fa-regular fa-pen-to-square"></i>
        </button>
        <button
          className="task-delete task-btn"
          onClick={() => props.deleteTask(props.id)}
        >
          <i className="fa-regular fa-trash-can"></i>
        </button>
      </div>
    </li>
  );
}
