import React from "react";
import "./tutorial.css";
export default function TutorialPage(props) {
  return (
    <div className={"tutorial-box " + props.classN + " box-" + props.index}>
      {props.title}
      {props.step === 13 ? (
        <button className="next" onClick={props.onClick}>
          {props.btn}
        </button>
      ) : (
        ""
      )}
      <div className="tutorial-btns">
        Done?{" "}
        <span className="skip" onClick={() => props.endTutorial()}>
          Click here to end tutorial.
        </span>
      </div>
    </div>
  );
}
